'use client'

import { useMutation, useQuery, useQueryClient } from '@tanstack/react-query'
import { Button, Card, TextField } from 'frosted-ui'
import { useState } from 'react'
import { useWhop } from '~/components/whop-context'
import { inboxQuery, sendMessageMutation, usersQuery } from '~/components/workouts/queries'

export default function InboxPage() {
  const { access, experience, user } = useWhop()
  const isAdmin = access.accessLevel === 'owner' || access.accessLevel === 'admin'
  const [selectedUserId, setSelectedUserId] = useState<string>('')
  const [message, setMessage] = useState('')
  const [search, setSearch] = useState('')
  const qc = useQueryClient()

  const { data: users } = useQuery({ ...usersQuery(experience.id, isAdmin ? search : undefined), enabled: isAdmin })
  const { data: messages } = useQuery(inboxQuery(experience.id, isAdmin ? (selectedUserId || undefined) : undefined))

  const sendMessage = useMutation({
    mutationFn: async () => sendMessageMutation(experience.id).mutationFn({ content: message, userId: isAdmin ? (selectedUserId || undefined) : undefined }),
    onSuccess: () => {
      setMessage('')
      qc.invalidateQueries({ queryKey: ['inbox', experience.id] })
    },
  })

  return (
    <div className="p-6 grid grid-cols-1 md:grid-cols-3 gap-4">
      {isAdmin && (
        <Card>
          <div className="p-4 space-y-2">
            <div className="text-sm opacity-70">Select user</div>
            <TextField.Input placeholder="Search" value={search} onChange={(e: any) => setSearch(e.target.value)} />
            <div className="max-h-64 overflow-auto space-y-1">
              {(users ?? []).map((u) => (
                <div key={u.id} className={`px-3 py-2 rounded cursor-pointer ${selectedUserId === u.id ? 'bg-black/5 dark:bg-white/10' : 'hover:bg-black/5 dark:hover:bg-white/10'}`} onClick={() => setSelectedUserId(u.id)}>
                  {u.name || u.username} <span className="opacity-70">@{u.username}</span>
                </div>
              ))}
            </div>
          </div>
        </Card>
      )}

      <Card className="md:col-span-2">
        <div className="p-4 flex flex-col gap-3">
          <div className="text-sm opacity-70">Conversation</div>
          <div className="min-h-56 max-h-96 overflow-auto space-y-2 p-2 rounded bg-black/5 dark:bg-white/10">
            {(messages ?? []).map((m) => (
              <div key={m.id} className={`px-3 py-2 rounded ${m.senderUserId === user.id ? 'bg-green-600/10' : 'bg-blue-600/10'}`}>
                <div className="text-xs opacity-70">{new Date(m.createdAt).toLocaleString()}</div>
                <div>{m.content}</div>
              </div>
            ))}
          </div>
          <div className="flex gap-2">
            <TextField.Input placeholder="Type a message" value={message} onChange={(e: any) => setMessage(e.target.value)} />
            <Button disabled={!message || (isAdmin && !selectedUserId) || sendMessage.isPending} onClick={() => sendMessage.mutate()}>Send</Button>
          </div>
        </div>
      </Card>
    </div>
  )
}


